<script>
export default {
  name: 'progressBar',
  props: {
    progress: {
      type: Number,
      default: 0
    }
  },
  computed: {
    progressLine() {
      let num = this.progressline
      return 'progressBar-' + num
    }
  }
}
</script>

<template src="./template.html"></template>
<style lang="scss" src="./style.scss" scoped></style>



